//MS Edge specific media query,
@supports (-ms-ime-align:auto) {
  .support-btn {
    width: 150px;
  }
  .menu-btn {
    position:relative;
  }
  .burger-box {
    position:absolute;
    top:24px;
    // right: 15px;
  }
}


//Firefox specific media query
@-moz-document url-prefix('') {
  .burger-box {
    top: 10px;
  }
}

//Internet explorer specific media query
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  /* IE10+ CSS styles go here */
  .container {
    margin: 0 15px;
  }
  .logo {
    min-height: 25px;
    height: auto;
  }
  .selection {
    ul li {
      margin-top: 28px;
    }
    .support-btn {
      width: 150px;
    }
    .menu-btn {
      position:relative;
    }
    .burger-box {
      position:absolute;
      top:24px;
      right: 15px;
    }
    .burger-menu {
      margin-right: 15px;
    }
  }
    .menu-btn {
      min-width: 63px;
    }
  }

//Chrome specific media query
@media all and (-webkit-min-device-pixel-ratio: 0) and (min-resolution: 0.001dpcm) {
  .selector:not(*:root),
  .burger-box {
   top: -3px;
  }
}

